
import Vue from "vue";
import VueRouter from "vue-router";
import Axios from 'axios';
import Home from "../views/Home.vue";
import Join from "../views/Join.vue";
import Member from "../views/Member.vue";
import House from "../views/House.vue";
import Interest from "../views/Interest.vue";
import InterestAdd from "../components/InterestAdd.vue";
import InterestDel from "../components/InterestDel.vue";
import InterestList from "../components/InterestList.vue";
import Board from "../views/Board.vue";
import axios from '@/common/axios.js';
import VueAlertify from 'vue-alertify'; 

// import { format } from 'core-js/core/date';
// import { BootstrapIconsPlugin  } from 'bootstrap-icons';

  // <a :href="'#' + i" @click="$router.push({name: 'member', params: {infoUpdate: information}})">{{i}} </a>
  // <router-link :to="{name: 'member', params: {id: 'member', no: i}}">{{i}} </router-link>

Vue.use(VueRouter);
Vue.use(VueAlertify);
Vue.prototype.$http = Axios;


const routes = [

  {
    path: "/",
    name: "Home",
    components: {
      'default': Home,
    }
  },
  {
    path: "/join",
    name: "Join",
    components: {
      'default': Join,
    }
  },

  {  
    path: "/member",
    name: "Member",
    meta: {authRequired: true},       //   
    components: {
      'default': Member,
    },
  },
  {
    path: "/house",
    name: "House",
    // meta: {authRequired: true},       //   
    components: {
      'default': House,
    }
  },
  {
    path: "/interest",
    name: "Interest",
    meta: {authRequired: true},       //   
    component: Interest,
    children: [
      {
        path: 'add',
        name: 'Add',
        component: InterestAdd,
      },
      {
        path: 'del',
        name: "Del",
        component: InterestDel,
      },
      {
        path: 'list',
        name: "List",
        component: InterestList,
      },
    ],
  },
  {
    path: '/board',
    name: "Board",
    component: Board,
    meta: {authRequired: true},       //   
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

//        : Nav Guard
router.beforeEach(function (to, from, next) {
  // console.log(sessionStorage.getItem("member"));
  if (to.matched.some(function (routeInfo) {        // meta : authRequired(true) 
    return routeInfo.meta.authRequired;
  })) {
    axios.get('/members/sessionCheck', {
    })
      .then(({ data }) => {
        console.log(data);
        if (data.result == 'login') {
          sessionStorage.removeItem("member");
          location.href = "/";                  //    next()  href  -> Navbar       
          alert(" .");
          // next('/');            //   
        }
        else {
          console.log("from -> to path: " + from.path + "=>" + to.path);
          next();
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  else {
    console.log("from -> to path: " + from.path + "=>" + to.path);
    next();                    //    
  }
});

export default router;
