<template>
  <nav aria-label="Page navigation">
    <ul class="pagination justify-content-center">
      <li v-if="prev" class="page-item">
        <a class="page-link" href="#" aria-label="Previous" @click="paginationChanged(startPageIndex - 1)">
          <span aria-hidden="true"></span>
        </a>
      </li>
      <li v-for="index in ( endPageIndex-startPageIndex + 1 )" :key="index"
          v-bind:class="{active: (startPageIndex + index - 1 == $store.state.board.currentPageIndex)}" class="page-item">
        <a @click="paginationChanged(startPageIndex + index - 1)" 
           class="page-link" href="#">{{ startPageIndex + index - 1 }}</a> <!-- href  ,   -->
      </li>
      <li v-if="next" class="page-item">
        <a class="page-link" href="#" aria-label="Next" @click="paginationChanged(endPageIndex + 1)">
          <span aria-hidden="true"></span>
        </a>
      </li>
    </ul>
  </nav>
</template>


<script>
export default {
  name: 'Pagination',
  // props  X
  // props: ['listRowCount', 'pageLinkCount', 'currentPageIndex', 'totalListItemCount'],
  data(){
    return {
    }
  },
  computed: {
    pageCount: function(){
      return Math.ceil(this.$store.state.board.totalListItemCount/this.$store.state.board.listRowCount);
    },
    startPageIndex: function(){
      if( (this.$store.state.board.currentPageIndex % this.$store.state.board.pageLinkCount) == 0 ){ //10, 20...
        return ((this.$store.state.board.currentPageIndex / this.$store.state.board.pageLinkCount)-1) * this.$store.state.board.pageLinkCount + 1
      }else{
        return  Math.floor(this.$store.state.board.currentPageIndex / this.$store.state.board.pageLinkCount) * this.$store.state.board.pageLinkCount + 1
      }
    },
    endPageIndex: function(){
      //   Error in render: "RangeError: Maximum call stack size exceeded" , this.endPageIndex   ,        
      // if( this.endPageIndex < this.pageCount ) return this.pageCount;

      let ret = 0;
      if( (this.$store.state.board.currentPageIndex % this.$store.state.board.pageLinkCount) == 0 ){ //10, 20...
        ret = ((this.$store.state.board.currentPageIndex / this.$store.state.board.pageLinkCount)-1) * this.$store.state.board.pageLinkCount + this.$store.state.board.pageLinkCount;
      }else{
        ret = Math.floor(this.$store.state.board.currentPageIndex / this.$store.state.board.pageLinkCount) * this.$store.state.board.pageLinkCount + this.$store.state.board.pageLinkCount;
      }
      //       
      return ( ret > this.pageCount ) ? this.pageCount : ret;
    },
    prev: function(){
      if( this.$store.state.board.currentPageIndex <= this.$store.state.board.pageLinkCount ){
        return false;
      }else{
        return true;
      }
    },
    next: function(){
      if( this.endPageIndex >=  this.pageCount){
        return false;
      }else{
        return true;
      }
    }
  },
  methods:{
    //  event 
    paginationChanged(pageIndex){
      console.log("paginationVue : paginationChanged : pageIndex : " + pageIndex );
      this.$emit('call-parent', pageIndex);
    }
  },

}
</script>

