<template>
        <div>
                        <div
                          class="row align-items-center justify-content-center interest-add"
                        >
                          <div class="col-2 my-2">
                            <select @change="dongSelect" v-model="guCode" name="location" id="location" class="form-select mr-sm-2">
                              <option selected disabled hidden value=""></option>
                              <option class="loc" value="11110"></option>
                              <option class="loc" value="11140"></option>
                              <option class="loc" value="11170"></option>
                              <option class="loc" value="11200"></option>
                              <option class="loc" value="11215"></option>
                              <option class="loc" value="11230"></option>
                              <option class="loc" value="11260"></option>
                              <option class="loc" value="11290"></option>
                              <option class="loc" value="11305"></option>
                              <option class="loc" value="11320"></option>
                              <option class="loc" value="11350"></option>
                              <option class="loc" value="11380"></option>
                              <option class="loc" value="11410"></option>
                              <option class="loc" value="11440"></option>
                              <option class="loc" value="11470"></option>
                              <option class="loc" value="11500"></option>
                              <option class="loc" value="11530"></option>
                              <option class="loc" value="11545"></option>
                              <option class="loc" value="11560"></option>
                              <option class="loc" value="11590"></option>
                              <option class="loc" value="11620"></option>
                              <option class="loc" value="11650"></option>
                              <option class="loc" value="11680"></option>
                              <option class="loc" value="11710"></option>
                              <option class="loc" value="11740"></option>
                            </select>
                          </div>
                          <div class="col-2 my-1">
                            <select class="form-select mr-sm-2" v-model="dongCode" id="dongSelect">
                              <option selected disabled hidden value=""></option>
                              <option v-for="(dong, idx) in dongList" :key="idx" :value="dong.dongCode">{{dong.dongName}}</option>
                            </select>
                          </div>
                          <div class="col-auto my-1">
                            <button @click="interestAdd" type="button" class="btn btn-outline-secondary" id="subBtn">
                              Search
                            </button>
                          </div>
                        </div>

                        </div>
    
</template>

<script>
import axios from '@/common/axios.js';

export default {
    name:"InterestList",
    data() {
      return {
        guCode: '',
        dongList: [],
        dongCode: '',
        interestList: [],
      }
    },
    mounted(){
      axios.get('/houses/interests',
      {})
      .then(({data}) => {
        console.log(data);
        this.interestList = data;
      })
      .catch((err) => {
        console.log(err);
      })
    },
    methods: {
      dongSelect() {
        console.log("dongSelect method");
        console.log(this.guCode);

        axios.get('/houses/dongSelect',
        {params: {
          guCode: parseInt(this.guCode),
        }})
        .then(({data}) => {
          console.log(data);
          this.dongList = data;
        })
        .catch((err) => {
          console.log(err);
        });
      },
      interestAdd() {

        let flag = false;
        this.interestList.forEach(el =>{
          if(el.dongCode == this.dongCode) flag = true;
        });

        if(flag){
          alert("  .");
          return;
        }

        console.log("interestAdd method");
        let $this = this;
        axios.post("/houses/interest", {
          guCode: parseInt($this.guCode),
          dongCode: parseInt($this.dongCode),
        })
        .then(({data}) => {
          console.log(data);
          this.interestList.push({dongCode: data.dongCode});
          alert("  .");
        })
        .catch((err) => {
          console.log(err);
        })
      },
    }
}
</script>

